@charset "UTF-8";
.prices {
  background-color: #f8f8f8; }
  .prices__main {
    padding-top: 16px;
    height: 270px; }
  .prices__service, .prices__name, .prices__price {
    position: relative; }
  .prices__service, .prices__name {
    grid-column: span 2; }
  .prices__key {
    display: block;
    color: #7e7e82; }
  .prices__value {
    color: #1b1c21; }
  .prices__list {
    display: flex;
    margin-top: 40px; }
    .prices__list-item {
      background-color: #ffffff;
      border-radius: 16px;
      width: 260px;
      height: 200px; }
    .prices__list-link {
      display: grid;
      gap: 20px;
      height: auto;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: auto auto auto;
      padding: 16px; }
  .prices__btn {
    color: white;
    background-color: #ff3e79;
    border-radius: 16px;
    height: 32px;
    border: none;
    text-transform: uppercase;
    font-family: 'TT Lakes bold';
    font-size: 12px;
    width: 100%;
    position: relative; }
    .prices__btn::before {
      content: '';
      width: 7px;
      height: 10px;
      right: 10px;
      bottom: 12px;
      position: absolute;
      background: url(./static/orderarrow.svg) no-repeat; }
  .prices li.prices__list-link {
    display: none; }
  .prices .price-text-footer {
    max-width: 421px;
    font-size: 14px;
    line-height: 20px;
    padding-left: 24px;
    padding-top: 24px; }
  .prices .prices__footer {
    border-bottom: 1px solid #d9fff5;
    padding: 20px; }
  .prices .prices__footer-text {
    padding-bottom: 30px; }
  .prices .commerce {
    font-weight: bold;
    position: relative; }
    .prices .commerce::before {
      content: '';
      width: 8px;
      height: 14px;
      top: 22px;
      right: -10px;
      background: url(./static/commercearrow.svg) no-repeat;
      position: absolute; }

@media (min-width: 334px) {
  .commerce {
    position: relative; }
    .commerce::after {
      content: '';
      width: 8px;
      height: 14px;
      top: 6px;
      right: -14px;
      background: url(./static/commercearrow.svg) no-repeat;
      position: absolute; }
    .commerce::before {
      display: none; } }

@media (min-width: 438px) {
  .prices__footer-text {
    width: 400px; } }

@media (min-width: 768px) {
  .prices__main {
    padding: 0 16px;
    height: auto; }
  .prices__service, .prices__name {
    grid-column: auto; }
  .prices__key {
    display: none; }
  .prices__list {
    border-radius: 10px;
    background-color: white;
    flex-direction: column; }
    .prices__list-item {
      background-color: transparent;
      border-radius: none;
      border-radius: 0;
      width: auto;
      height: auto;
      padding: 0 16px;
      border-radius: inherit; }
    .prices__list-link {
      display: grid;
      gap: 20px;
      height: auto;
      grid-template-columns: 4fr 1fr 1fr 1fr;
      grid-template-rows: auto;
      width: auto; }
  .prices li.prices__list-link {
    background-color: #f8f8f8;
    color: #7e7e82;
    padding-left: 40px;
    padding-top: 25px;
    padding-bottom: 16px;
    display: grid; }
  .prices__list-item:first-child .prices__service::before,
  .prices__list-item:first-child .prices__name::before,
  .prices__list-item:first-child .prices__price::before {
    content: '';
    left: 0;
    top: -46px;
    position: absolute;
    z-index: 2;
    color: #7e7e82; }
  .prices__list-item:first-child .prices__service::before {
    content: 'Ремонтные услуги'; }
  .prices__list-item:first-child .prices__name::before {
    content: 'Цена'; }
  .prices__list-item:first-child .prices__price::before {
    content: 'Срок'; } }
